
.content-header{
    text-align: start;
    color: #F14A3A;
    margin: 0px;
}
.content-description{
    word-wrap: break-word;   
    min-width: 100%;
    width: 0; 
    color: #F6F6F6;
    margin: 0px;
}

.content-img{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    max-height: 40vh;
    max-width: 100%;
}

.content-container{
    display: inline-block;
    flex-direction: row;
}