
.project-header{
    color: #F14A3A;
    margin-bottom: 0px;
}
.project-descripition{
    color: #F6F6F6;
    margin-top: 0px;
}
.project-container{
    width: 70%;
    display: flex;
    flex-direction: column;
    margin-top: 5vh;
    border-color: #AD8174;
    border-width: 5px;
    border-style: double none none none;
    position: relative;
}
.project-contents-list{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
    width: 80%;
    align-self: center;
}

.github-link{
    position: absolute;
    top: 0px;
    right: 0px;
    width: 30px;
}
